.header {
  height: 60px;
  line-height: 60px;
  padding: 0 20px;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1;
  margin-top: 20px;
  font-family: "Pretendard-Light", sans-serif;
}

.header_items {
  display: flex;
  justify-content: center;
}

.header_items > a {
  color: #fff;
  font-size: 20px;
  font-weight: 600;
  text-decoration: none;
  margin: 0 20px;
}

a:hover {
  text-decoration: underline;
}

@media screen and (max-height: 890px) {
  .header {
    height: 30px;
    line-height: 30px;
  }
}